import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import BuyBids from './BuyBids';
import './index.css';
import reportWebVitals from './reportWebVitals';

const appAnchorElement = document.getElementById('wp-wallet-adapter-wrapper')
ReactDOM.render(
    <React.StrictMode>
        <App/>
    </React.StrictMode>,
    appAnchorElement
);

const buyBidsButtons = document.getElementsByClassName('buy-bids-wrapper');
if (0 !== buyBidsButtons.length) {
    for (let i = 0; i < buyBidsButtons.length; i++) {
        ReactDOM.render(
            <React.StrictMode>
                <BuyBids/>
            </React.StrictMode>,
            buyBidsButtons[i]
        );
    }
}

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
